import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'


/**
 * 角色列表
 */

export function queryRolePort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryRole',
	    method: 'get',
	    params:query,
  	});
}


/**
 * 删除角色
 */

export function deleteRolePort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'deleteRoleByID',
	    method: 'get',
	    params:query,
  	});
}


/**
 * 新增角色
 */

export function saveRolePort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'saveRole',
	    method: 'get',
	    params:query,
  	});
}


/**
 * 更新角色记忆
 */

export function queryRoMeCaByRoIdPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryRoMeCaByRoId',
	    method: 'get',
	    params:query,
  	});
}

/**
 * 更新角色
 */

export function updateRolePort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'updateRole',
	    method: 'get',
	    params:query,
  	});
}




