import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'

/**
 * 查询用户信息
 */

export function getUsersPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryUser',
	    method: 'get',
	    params:query,
  	});
}

/**
 * 新增用户信息
 */

export function saveUsersPort(query) {
  	return fetch({
	    url: publicConst.proxyPath+'saveUser',
	    method: 'get',
	    params:query,
  	});
}

/*
* 删除用户信息
*/
export function deletePLUserPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'deletePLUserByID',
	    method: 'get',
	    params:query,
	});
}

/*
* 更新用户信息
*/
export function updateUserPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'updateUser',
	    method: 'get',
	    params:query,
	});
}

/*
* BO查询
*/
export function serchBoPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'queryBo',
	    method: 'get',
	    params:query,
	});
}


/*
* 保存用户和BO的关系
*/
export function saveUserObjectPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'saveUserObject',
	    method: 'get',
	    params:query,
	});
}


/*
* 保存用户BO回显
*/
export function queryBoByUserPost(query) {
	return fetch({
	    url: publicConst.proxyPath+'queryBoByUser',
	    method: 'get',
	    params:query,
	});
}



